
.header{
  position: relative; 
  overflow: hidden;
  color: #fff;
  width: 100%;  
  background-color: rgba(7,17,27,.5);
  border-bottom: 1px solid #d9dde1;
}

/* header内容区域 */
.header_cont_wrap{
  padding: 24px 12px 18px 24px; 
  font-size: 0;
}
.header .avatar,.header .avatar>img{
  width: 72px;
  height: 72px;
  border-radius: 5px;
}
.header .avatar{
  display: inline-block;
  padding-right: 16px;
}
.header_cont{
  display: inline-block;
  vertical-align: top;
  padding: 4px 0;
}
.header_cont .title{
  padding-bottom: 6px;
}
.header_cont .brand{
  display: inline-block;
  padding-right: 6px;
}
.header_cont h3{
  display: inline-block;
  vertical-align: top;
  font-size: 16px;
  line-height: 24px;
  font-weight: bold;
}
.header_cont .distribution{
  font-size: 14px;
  font-weight: 200;
  line-height: 14px;
  padding-bottom: 8px;
}
.header_cont .supports span{
  padding-left: 4px;
  font-size: 12px;
  line-height: 14px;
  vertical-align: top;
}
.more_btn{
  position: absolute;
  font-family: 'consolas';
  background-color: rgba(7,17,27,.2);
  top: 75px;
  right: 15px;
  padding: 6px 8px;
  border-radius: 15px;
  font-size: 12px;
  color: #fff;
  font-weight: 200;
  line-height: 12px;
}

/* 公告 */
.bulletin{
  position: relative;
  background-color: rgba(7,17,27,.2);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  height: 22px;
  line-height: 22px;
  padding: 0 36px 0 15px;
}
.bulletin img{
  padding-top: 3px;
  vertical-align: top;
}
.bulletin span{
  vertical-align: top;
  font-size: 12px;
}
.bulletin_btn{
  position: absolute;
  right: 20px;
  top: 0;
  font-family: 'consolas';
}
/* 背景莫糊图 */
.bf_img{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  -webkit-filter:blur(10px);
          filter:blur(10px);
}
.bf_img img{width: 100%;height: 100%;
}
/* 详情页 */
.details_page{
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  height: 100%;
  width: 100%;
  background-color: rgba(7,17,27,.8);
  opacity: 1;
  overflow: auto; /* 1) 内容溢出时出现滚动条 */
  text-align: center;
}
.details_wrap{
  min-height: 100%; /* 2) 设置外层容器的最小高度为100%; */
}
.details_cont{
  padding: 40px 40px 64px;  /* 3) 设置内容区域的padding值,下padding值与按钮的margin-top值对应 */
}
.details_close{
  position: relative;   /* 4) 设置相对定位,并向上移动按钮,左右自动 */
  margin: -64px auto 0 auto;  
  clear: both;  /* 5) 清除浮动 */
  height: 32px;
  width: 32px;
  font-size: 20px;
}
.details_title{
  font-size: 20px;
  font-weight: 600;
  color: #fff;
  line-height: 20px;
}
.star_icon{
  height: 24px;
  line-height: 24px;
  padding: 16px 0 28px 0;
}
.star_icon img{
  vertical-align: middle;
}
.details_line{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-bottom: 24px;
}
.line{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  position: relative;
  top: -6px;
  border-bottom: 1px solid rgba(255,255,255,.2);
}
.details_line_txt{
  font-size: 16px;
  margin: 0 24px; 
  font-weight: 700;
}
.detail-fade-enter-active,.detail-fade-leave-active{
   transition: all .3s ease-out;
}
.detail-fade-enter,.detail-fade-leave-active {
  opacity: 0;
}
.details_supports{
  padding-left: 24px;
  padding-bottom: 20px;
}
.details_supports>li{
  text-align: left;
}
.details_supports>li>p{
  display: inline-block;
  height: 22px;
  font-size: 14px;
  font-weight: 200;
  line-height: 14px;
}
.details_supports>li>img{
  padding-right: 12px;
}
.details_bulletin{
  text-align: left;
  padding: 0 24px;
  font-size: 14px;
  font-weight: 200;
  line-height: 22px;
}


/* 导航 */
.header_nav{
  background-color: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
}
.header_item{
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
}
.header_item a{
  display: block;
  line-height: 40px;
  font-size: 14px;
  color: rgb(77,85,93);
}
.header_item a.active{
  color: rgb(240,20,20);
}




